table {
    font-size: 1em;
    border-collapse: collapse;
    text-align: center;
}
th, td:first-child {
    background: rgb(100, 65, 164);
    color: white;
    padding: 10px 20px;
}
th, td {
    border-style: solid;
    border-width: 0 1px 1px 0;
    border-color: #999;
}


